.loadingWrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(000, 000, 000, 0.7);
    display: flex;
    justify-content: center;
    z-index: 2;
}

.loading {
    position: fixed;
    width: 50px;
    height: 50px;
    top: 400px;
    left: 50%;
    transform: translateX(-50%);
}